.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login {
  border-radius: 10px;
  height: 200px;
  width: 200px;
  border: 1px solid red;
}

.login_input {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.input_fild{
  text-align: center;
  padding: 5px;
  margin: 2px;
  outline: none;
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: hidden;
  border-radius: 10px;
  
}

.button_login {
  display: flex;
  justify-content: center;
  margin-top: 5px;
}

.login_title {
  text-align: center;
  padding: 10px;
  border-radius: 5px;
}
